export default function App() {
  const [midiJSON, setMidiJSON] = useState("");
  const download = () => {
    var midi = new Midi();
    const track = midi.addTrack();
    var midiList = JSON.parse(midiJSON);
    midiList.tracks[0].notes.forEach((e) => {
      track.addNote({
        midi: e.midi,
        time: e.time,
        duration: e.duration,
        velocity: Number(e.velocity)
      });
    });
    const midiFile = new File([midi.toArray()], "midi.mid");
    saveAs(midiFile, "midi.mid");
  }
  return (
    <div style={{ paddingTop: 40, flexDirection: 'column' }} className="App">
      <p>TONE.JS Json</p>
      <textarea placeholder={"MIDI JSON"} rows={10} style={{ width: '80%' }} onChange={(text) => {
        setMidiJSON(text.target.value)
        console.log(text.target.value)
      }} />
      <div>
        <button style={{marginTop:20}} onClick={() => download()}>Download Midi</button>
      </div>
    </div>
  );
}